<template>
  <div class="postCommentBox">
    <div>
      <p
        class="textMax"
        :style="textValue.length >= 50 ? ('color: #eb5253;') : ('color: #222222;')"
      >
        {{textValue.length}}/50
      </p>
      <textarea
        rows="4"
        cols="40"
        placeholder="请输入评论内容"
        maxlength="50"
        autofocus="autofocus"
        v-model.trim="textValue"
      >
      </textarea>
    </div>
    <p @click="putText">发布</p>
  </div>
</template>

<script>
import { putText } from '@/api/comments.js'
export default {
  name: 'postComments',
  props: {
    // 接收文章id
    articleId: {
      type: [String, Number, Object],
      required: true
    }
  },
  data () {
    return {
      textValue: '', // 评论内容
      target: this.articleId.toString(), // 评论的目标id
      art_id: null // 文章id(回复评论时才会用)
    }
  },
  methods: {
    // 请求发布评论
    async putText () {
      // 没有内容 发布按钮就无效
      if (!this.textValue) {
        return
      }
      // loading
      this.$toast.loading({
        message: '发布中...',
        forbidClick: true,
        duration: 0
      })
      try {
        await putText({
          target: this.target,
          content: this.textValue,
          art_id: this.art_id
        })
        this.$toast.success({
          message: '发布成功',
          duration: 500
        })
        this.textValue = '' // 发布成功后清空输入框内容
        // 发布成功关闭弹出层
        this.$emit('closeComment', this.textValue)
      } catch (err) {
        // console.log(err)
        this.$toast.fail({
          message: '发布失败',
          duration: 500
        })
      }
    }
  }
}
</script>

<style scoped>
  .postCommentBox{
    margin: 10px;
    box-sizing: border-box;
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  .postCommentBox>p{
    font-size: 14px;
    color: #1989fa;
  }
  textarea{
    background-color: #f8f8f8;
    border: unset;
    font-size: 14px;
    resize:none
  }
  .textMax{
    position: absolute;
    bottom: 10px;
    right: 80px;
    font-size: 13px;
    color: #888;
  }
</style>
