<!--文章下面的菜单-->
<template>
  <div class="box">
    <!--发布评论的弹出层-->
    <van-popup v-model="showComment" position="bottom">
      <postComments :articleId="articleId" @closeComment="closeComment"></postComments>
    </van-popup>
    <div class="artSearch" @click="showComment = true">
      评论
    </div>
    <van-icon class-prefix='icon'
              class='artPl'
              name="wenda"
              :badge="comCount"/>
    <van-icon :color="articleData.is_collected ? '#ffd801' : '#888'"
              :name="articleData.is_collected ? 'star' : 'star-o'"
              @click="onCollection"/>
    <van-icon :color="articleData.attitude === 1 ? '#fa1616' : '#888'"
              :name="articleData.attitude === 1 ? 'good-job' : 'good-job-o'"
              @click="onPraise"/>
    <van-icon name="share" />
  </div>
</template>

<script>
// 收藏/取消收藏文章 点赞/取消点赞文章
import { ArticleCollection, ArticleUncollection, ArticlePraise, ArticleUnpraise } from '@/api/article'
// 写评论的组件
import postComments from './postComments'

export default {
  name: 'bottomColumn',
  data () {
    return {
      showComment: false
    }
  },
  components: {
    postComments
  },
  props: {
    // 接收文章id（用于文章收藏/点赞...）
    articleId: {
      require: true
    },
    // 接收文章内容
    articleData: {
      require: true
    },
    // 接受评论条数
    comCount: {
      type: [Number, String],
      require: true
    }
  },
  methods: {
    // 收藏/取消收藏文章
    async onCollection () {
      // loding等待请求
      this.$toast.loading({
        message: '操作中...',
        forbidClick: true,
        duration: 0
      })
      try {
        // 判断是否收藏了 收藏了点击就是取消，反之亦然
        if (this.articleData.is_collected) {
          await ArticleUncollection(this.articleId)
          this.articleData.is_collected = false
          this.$toast.success({
            message: '已取消收藏',
            duration: 1000
          })
        } else {
          await ArticleCollection(this.articleId)
          this.articleData.is_collected = true
          this.$toast.success({
            message: '已收藏',
            duration: 1000
          })
        }
      } catch (err) {
        this.$toast.fail({
          message: '操作失败',
          duration: 1000
        })
      }
    },
    // 点赞/取消点赞文章
    async onPraise () {
      // loding等待请求
      this.$toast.loading({
        message: '操作中...',
        forbidClick: true,
        duration: 0
      })
      try {
        // 判断是否点赞了 点赞了点击就是取消，反之亦然
        if (this.articleData.attitude === 1) {
          await ArticleUnpraise(this.articleId)
          this.articleData.attitude = -1
          this.$toast.success({
            message: '已取消',
            duration: 1000
          })
        } else {
          await ArticlePraise(this.articleId)
          this.articleData.attitude = 1
          this.$toast.success({
            message: '已点赞',
            duration: 1000
          })
        }
      } catch (err) {
        this.$toast.fail({
          message: '操作失败',
          duration: 1000
        })
      }
    },
    // 发布成功关闭弹出层 （textValue是发布的评论内容）
    closeComment (textValue) {
      this.showComment = false
      // 评论内容传上去用于更新视图
      this.$emit('showCom', textValue)
    }
  }
}
</script>

<style scoped>
  .box{
    padding: 0 14px;
    height: 49px;
    width: 100%;
    background-color: #fff;
    box-sizing: border-box;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
  }
  .box>i{
    color: #888;
  }
  .artSearch{
    height: 35px;
    width: 160px;
    border: 1px solid rgba(0,0,0,0.2);
    border-radius: 20px;
    color: #888;
    font-size: 14px;
    line-height: 35px;
    text-align: center;
  }
  .artPl{
    position: relative;
    padding: 0;
  }
</style>
