<!--评论模块-->
<template>
  <div>
    <h5 class="commentsTitle">-----------------全部评论-----------------</h5>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <!--拿到了评论数据-->
      <van-cell v-for="(item, index) in list" :key="index">
        <comment solt="default" :commentData="item"></comment>
      </van-cell>
    </van-list>
  </div>
</template>

<script>
import { commentsList } from '@/api/comments.js'
import comment from './comment'
export default {
  name: 'comments',
  created () {},
  components: {
    comment // 评论项
  },
  props: {
    // 接收文章id
    articleId: {
      type: [String, Number, Object],
      required: true
    },
    // 发布评论的内容
    textValue: {
      type: String
    }
  },
  data () {
    return {
      list: [], // 存放评论列表数据
      loading: false,
      finished: false,
      offset: null, // 页面（不传有默认的）
      limit: 10 // 每页条数
    }
  },
  methods: {
    // 列表业务
    async onLoad () {
      // 请求获取评论列表
      const { data } = await commentsList({
        type: 'a', // a文章评论 c评论回复
        source: this.articleId, // 文章id
        offset: this.offset, // 页码（last_id作为下一页页码。可以不传）
        limit: this.limit
      })
      // console.log(data.data.total_count)
      this.$emit('comData', data.data.total_count) // 发送评论数量过去用于角标设置
      this.offset = data.data.last_id // 将此次请求的last_id作为下一页的页码
      this.list.push(...data.data.results) // 添加到数据列表
      this.loading = false // 加载状态结束
      // 数据全部加载完成
      if (data.data.results.length < this.limit) {
        this.finished = true
      }
    }
  },
  watch: {
    textValue () {
      // 做个伪数据添加到列表
      this.list.unshift({
        content: this.textValue
        // is_top: 0,
        // aut_photo,
        // aut_name
      })
    }
  }
}
</script>

<style scoped>
  .van-cell{
    padding: unset;
  }
  .commentsTitle{
    padding-top: 20px;
    color: #eb5253;
    font-size: 16px;
    display: flex;
    justify-content: center;
  }
</style>
