<!--文章详情中的用户信息-->
<template>
  <div class="userInfo-box">
    <div class="left-user-info">
      <img :src="articleData.aut_photo" alt="">
      <div>
        <span>{{articleData.aut_name}}</span>
        <span>{{articleData.pubdate | days}}</span>
      </div>
    </div>
    <!-- 判断是否关注该用户 -->
    <div @click="onUser" :class="(articleData.is_followed ? 'usefo' : 'usefo1')+' '+'right-user-info'">
      {{articleData.is_followed ? '已关注' : '+ 关注'}}
    </div>
  </div>
</template>

<script>
import { addUser, delUser } from '@/api/user.js'

export default {
  name: 'userInfo',
  data () {
    return { }
  },
  props: {
    articleData: {
      require: true
    }
  },
  methods: {
    async onUser () {
      // loding弹出框
      this.$toast.loading({
        message: '操作中...',
        forbidClick: true,
        duration: 0
      })
      try {
        // 已关注，点击就取消关注, 反之就关注
        if (this.articleData.is_followed) {
          await delUser(this.articleData.aut_id) // 请求取消关注
        } else {
          await addUser(this.articleData.aut_id) // 请求关注
        }
        this.articleData.is_followed = !this.articleData.is_followed // 用于更新视图（不然不会实时更新）
        this.$toast.clear() // 请求完成就关闭loding提示框！
      } catch (err) {
        this.$toast.fail('网络异常，稍后再试') // 请求失败提示框
      }
    }
  }
}
</script>

<style scoped>
  .userInfo-box{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .left-user-info{
    display: flex;
  }
  .left-user-info>img{
    width: 40px;
    height: 40px;
    border-radius: 100%;
  }
  .left-user-info>div{
    padding-left: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .left-user-info>div>*:nth-child(1){
    font-size: 14px;
    color: #222;
    padding-bottom: 5px;
  }
  .left-user-info>div>*:nth-child(2){
    font-size: 12px;
    color: #888;
  }
  .right-user-info{
    height: 30px;
    font-size: 14px;
    border-radius: 20px;
    line-height: 30px;
    padding: 0 15px;
  }
  .usefo1{
    background-color: #1989fa;
    color: #fff;
  }
  .usefo{
    background-color: unset;
    border: 1px solid #888;
    color: #222;
  }
</style>
