<!--文章详情组件-->
<template>
  <div class="article-box">
    <!--顶部导航栏-->
    <navbar class="activeNavabbar">
      <i slot="left" class="icon icon-youjiantou" @click="cloneM"></i>
      <span slot="title" class="title">文章详情</span>
    </navbar>
    <!--文章内容-->
    <div class="artcle-user">
      <p class="user-titel">{{articleData.title}}</p>
      <!--用户信息-->
      <userInfo :articleData="articleData"></userInfo>
      <!--正文,这里用了github的markdown的样式表-->
      <div class="markdown-body" v-html="articleData.content" ref="act">
      </div>
      <!--评论-->
      <comments :articleId="articleId" :textValue="textValue" @comData="comData"></comments>
    </div>
    <!--底部工具栏-->
    <bottomColumn class="bottomColumn" :articleId="articleId" :articleData="articleData" @showCom="showComS" :comCount="comCount"></bottomColumn>
  </div>
</template>

<script>
import navbar from '@/components/navbar'
import bottomColumn from './bottomColumn.vue'
import userInfo from './userInfo.vue'
import comments from './comments/index.vue'
// github的markdown的样式表,用于正文样式
import '@/styles/github-markdown.css'
import { getArticleData } from '@/api/article'

// 引入vant中的图片预览
import { ImagePreview } from 'vant'

export default {
  name: 'index',
  data () {
    return {
      articleData: {}, // 接收请求到的文章详情页的数据
      textValue: '', // 发布评论的内容
      comCount: '' // 评论条数
    }
  },
  components: {
    navbar,
    bottomColumn,
    userInfo,
    comments
  },
  props: {
    // 通过props来接收使用该路由的组件动态传递过来的参数（在这个路由组件中就能直接this.articleId来使用）（需要该路由设置props:true开启props传参）
    articleId: {
      type: [String, Number, Object],
      required: true
    }
  },
  created () {
    // 动态路由传递过来的参数 (就不用this.$route.params.xxx来接收参数了)
    // console.log(this.$route.params)
    // console.log(this.articleId)
    this.gArticleData()
  },
  methods: {
    // 请求文章内容
    async gArticleData () {
      try {
        const { data } = await getArticleData(this.articleId)
        console.log(data)
        this.articleData = data.data
        // 在DOM更新完成后再找到ref为act下的所有img标签
        this.$nextTick(() => {
          this.yulanImg()
        })
      } catch (err) {
        console.log(err)
      }
    },
    // 预览图片的方法
    yulanImg () {
      // 存放该文章所有的图片url
      const imgArr = []

      // 获取文章DOM节点
      const act = this.$refs.act
      // 找到这个文章中所有的img标签
      const imgList = act.querySelectorAll('img')
      imgList.forEach((item, index) => {
        // console.log(item.src)
        imgArr.push(item.src)
        // 每个img标签添加点击事件，点击就打开预览图片
        item.onclick = () => {
          // 预览图片的方法(vant组件库的预览图片组件的功能方法)
          ImagePreview({
            // 图片数组
            images: imgArr,
            // 起始预览图片
            startPosition: index
          })
        }
      })
    },
    // 接受发布评论的内容，用于更新视图
    showComS (textValue) {
      this.textValue = textValue
    },
    // 评论条数
    comData (value) {
      this.comCount = value
    },
    // 关闭搜索页面
    cloneM () {
      // 添加缓存
      // this.$store.commit('addCould', 'TabbarIndex')
      // 返回上级页面
      this.$router.back()
    }
  }
}
</script>

<style scoped>
  .theme-colors {
      background-color: #1989fa;
  }
  .article-box{
    background-color: #fff;
    position: fixed;
    top: 0;
    width: 100%;
  }
  .icon-youjiantou{
    color: #fff;
  }
  .title{
    font-size: 15px;
    color: #fff;
  }
  .bottomColumn{
    position: fixed;
    bottom: 0
  }
  .artcle-user{
    padding: 0 14px;
    overflow-y: scroll;
    height: 572px;
  }
  .user-titel{
    font-size: 20px;
    color: #222222;
    padding-top: 20px;
    padding-bottom: 20px;
    font-weight: bold;
  }
  .markdown-body{
    margin-top: 20px;
  }
</style>
