import request from '@/utils/request'

// 请求评论列表
export const commentsList = params => request({
  url: '/app/v1_0/comments',
  method: 'GET',
  params
})

// 请求点赞评论（需要单条评论的id）
export const onLick = target => request({
  url: '/app/v1_0/comment/likings',
  method: 'POST',
  data: {
    target
  }
})

// 请求取消点赞评论（需要单条评论的id）
export const delLick = target => request({
  url: `/app/v1_0/comment/likings/${target}`,
  method: 'DELETE'
})

// 请求发布
export const putText = data => request({
  url: '/app/v1_0/comments',
  method: 'POST',
  data
})
