<!--封装每一条评论的内容展示-->
<template>
  <div class="comment-box">
    <img :src="commentData.aut_photo" alt="">
    <div>
      <div>
        <p>{{commentData.aut_name}}</p>
        <p>
          <van-icon :name="commentData.is_liking ? 'good-job' : 'good-job-o'" @click="onLick"/>
          {{commentData.like_count}}
        </p>
      </div>
      <p>{{commentData.content}}</p>
      <div>
        <!--调用自定义的格式化时间全局过滤器，定义有参数(格式化时间的具体格式)-->
        <p>{{commentData.pubdate | daysFormat('YYYY-MM-DD HH:mm')}}</p>
        <div>
          <p>{{commentData.reply_count}} 回复</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// 请求点赞评论
import { onLick, delLick } from '@/api/comments'

export default {
  name: 'comment',
  props: {
    // 接收评论数据
    commentData: {
      type: Object,
      required: true
    }
  },
  methods: {
    // 点击点赞
    async onLick () {
      const commentId = this.commentData.com_id.toString() // 单条评论的id
      // 已点赞
      if (this.commentData.is_liking) {
        await delLick(commentId)
        this.commentData.like_count -= 1 // 评论加一
      // 没有点赞，进行点赞
      } else {
        await onLick(commentId)
        this.commentData.like_count += 1 // 评论减一
      }
      this.commentData.is_liking = !this.commentData.is_liking
    }
  }
}
</script>

<style scoped>
  .comment-box{
    display: flex;
    padding: 10px 0;
  }
  .comment-box>img{
    width: 35px;
    height: 35px;
    border-radius: 20px;
    margin-right: 15px;
  }
  .comment-box>div{
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  .comment-box>div>div:nth-child(1){
    display: flex;
    justify-content: space-between;
  }
  /*名称*/
  .comment-box>div>div:nth-child(1)>p:nth-child(1){
    font-size: 14px;
    color: #1989fa;
  }
  .van-icon-good-job::before {
    color: #eb5253;
  }
  /*内容*/
  .comment-box>div>p{
    color: #222222;
    font-size: 14px;
    font-weight: bold;
    box-sizing: border-box;
    padding-right: 5px;
  }
  .comment-box>div>div:nth-child(3){
    display: flex;
    align-items:center;
  }
  /*时间*/
  .comment-box>div>div:nth-child(3)>p{
    font-size: 8px;
    color: #888;
    margin-right: 10px;

  }
  /*回复*/
  .comment-box>div>div:nth-child(3)>div{
    height: 18px;
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 50px;
    /*border: 1px solid #cbcbcb;*/
  }
  .comment-box>div>div:nth-child(3)>div>p{
    padding: 0 7px;
    font-size: 11px;
    color: #fff;
    line-height: 18px;
  }

  .comment-box>div>div:nth-child(3){
    display: flex;
  }
</style>
